.footer {
  height: 200px;
  background-image: url(../images/photos/footer_mobile.jpg);

  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  
  @include onTablet {
    height: 400px;
    background-image: url(../images/photos/footer_tablet.jpg);
  }

  @include onDesktop {
    height: 460px;
    background-image: url(../images/photos/footer_desktop.jpg);
  }
}


